.backdrop {
  width: 100%;
  height: 100vh;
  opacity: 1;
  z-index: 999;
  background: rgba(94, 99, 99, .7);
  transition: opacity .25s;
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  width: 320px;
  height: 449px;
  background: #ffe550;
  border-radius: 30px;
  padding: 20px;
  transition: transform;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 4px 4px rgba(0, 0, 0, .06);
}

@media screen and (min-width: 768px) {
  .modal {
    width: 458px;
    padding: 32px;
  }
}

.modal__title {
  text-align: center;
  margin-top: 12px;
  font-size: 24px;
  font-weight: 700;
  line-height: 1.21;
}

.modal__form-element {
  flex-direction: column;
  margin-top: 24px;
  display: flex;
}

.modal__form-label {
  font-size: 16px;
  font-weight: 500;
  line-height: 1, 38;
  margin-left: 12px;
}

.modal__input-modal {
  width: 100%;
  height: 48px;
  background: #fff;
  border: 1px solid rgba(18, 18, 18, .12);
  border-radius: 30px;
  margin-top: 2px;
  padding-left: 12px;
  font-size: 16px;
  line-height: 1.25;
  position: relative;
  box-shadow: inset 0 2px 4px rgba(0, 0, 0, .15);
}

.modal__input-modal:hover, .modal__input-modal:focus {
  outline: rgba(0, 0, 0, 0);
}

.modal__form {
  margin-bottom: 20px;
}

.modal__contract {
  margin-left: 6px;
  text-decoration: underline;
}

.modal__form-field {
  letter-spacing: .01em;
  font-size: 12px;
  line-height: 1.17;
  position: relative;
}

.modal__input-modal:not(:placeholder-shown):required:valid {
  border-color: #7eb101;
}

.modal__input-modal:not(:placeholder-shown):required:invalid {
  border-color: #ff705d;
}

.is-hidden {
  visibility: hidden;
  pointer-events: none;
  transition: visibility;
}

.modal__close-button {
  width: 32px;
  height: 32px;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, .06));
  cursor: pointer;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: fill;
  display: flex;
  position: absolute;
  top: 16px;
  right: 16px;
}

.modal__close-button:hover, .modal__close-button:focus {
  fill: #7eb101;
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scale(.4);
}

.modal__form-field svg {
  position: absolute;
  top: 50%;
  left: 12px;
}

.modal__form-element input:focus {
  border: 1px solid #bbf330;
}

.modal__form-element input:focus + svg {
  fill: #bbf330;
  transition: fill;
}

.modal__form .btn {
  border: none;
  border-radius: 4px;
  padding: 10px 52px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

