.section__offerings {  
    margin: 0 auto;
    padding-top: 41px;
    

    background: linear-gradient(to bottom, transparent 0%, transparent 90px, var(--maim-bg-color-and-border) 8.2%, var(--maim-bg-color-and-border) 100%), url(../images/offerings/offerings-bg-m.svg) 50% 0 no-repeat;
    background-size: auto auto;
        
    @include mobile-max {
        max-width: $mobile;
        
    }
    @include tablet {   
        width: $mobile;
        margin-top: -254px;
    }
    @include tablet {
        
        background: linear-gradient(to bottom, transparent 0%, transparent 110px, var(--maim-bg-color-and-border) 8.2%, var(--maim-bg-color-and-border) 100%), url(../images/offerings/offerings-bg-t.svg) 50% 0px no-repeat;
        padding-top: 112px;
        width: $tablet;
    }
    @include pc {
        background: linear-gradient(to bottom, transparent 0%, transparent 190px, var(--maim-bg-color-and-border) 8.2%, var(--maim-bg-color-and-border) 100%), url(../images/offerings/offerings-bg-t.svg) 50% 0px no-repeat;
        padding-top: 177px;
        padding-bottom: 43px;
        width: $pc;
    }
}

.offerings__warps {
    
    @include pc {
        display: flex;
        align-items: center;
        
        gap: 90px;
    }
}
.offerings__image {
   margin-bottom: 60px;
    @include pc {
        margin-bottom: 0;
    }
}
.offerings__info {
    @include pc {
        width: 573px;
    }
    
}
.offerings__icon {
    display: block;       
    margin: 0 auto;
    
}
.offerings__title {
    margin-bottom: 28px;
    font-size: 28px;
    line-height: 1.18;
    @include mobile-max {
        max-width: 280px;
    }
    @include tablet {
        margin-bottom: 20px;
        font-size: 42px;
        line-height: 1.17;
        width: 528px;
    }

    @include pc {        
        line-height: 1.2;
    }
}
.offerings__paragraph {
    margin-bottom: 28px;
    font-size: 14px;
    line-height: 1.14;

    
    @include tablet {
        margin-bottom: 50px;
        font-size: 16px;
        line-height: 1.19;
        max-width: 500px;
    }

    @include pc {
        margin-bottom: 40px;
        line-height: 1.5;
    }
}
.advantages {
    &__list {
        margin-bottom: 28px;

        @include tablet {
            display: flex;
            gap: 116px;
            margin-bottom: 40px;
        }
        @include pc {
            margin-bottom: 32px;
            gap: 67px;
        }
    }

    &__item {
        &:first-child {
            margin-bottom: 28px;     
            
        }
        &:first-child::before {
            content: "";
            display: block;
            width: 48px;
            height: 48px;
            background: var(--header-footer-bg) url(../images/offerings/rocket.svg) 50% 50% no-repeat;
            border-radius: 50%;
        }

        &:last-child::before {
            content: "";
            display: block;
            width: 48px;
            height: 48px;
            background: var(--header-footer-bg) url(../images/offerings/touch.svg) 50% 50% no-repeat;
            border-radius: 50%;
        }
        
    }
    &__title {
        margin-top:8px ;
        margin-bottom: 16px;

        font-size: 18px;
        line-height: 1.17;
        
        
        @include pc {
            line-height: 1.4;
        }
    }
    &__paragraph {
        font-size: 14px;
        line-height: 1.14;

        @include tablet {
            width: 253px;
        }
        @include pc {
            line-height: 1.5;
        }
    }
}


.offerings__room {
    display: flex;
    align-items: center;
    gap: 23px;
}
.offerings__link {
    display: flex;
    align-items: center;
    gap: 7px;
    padding: 7px 0;
    text-decoration: none;

    font-size: 13px;
    line-height: 1.5;


    &:active {
        color: var(--title-color-text);
        fill: var(--title-color-text);
    }
    @include pc {
        display: none;
    }
}